import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import { useFormik } from 'formik';
import { useNavigate } from 'react-router-dom';
import Close from '../../icons/Close.svg';
import * as Yup from 'yup';

import { authOperations } from '../../redux/app/auth';

import {
  ContainerRegistr,
  TitleRegistr,
  CloseBtnPosition,
  CloseBtn,
  CloseImg,
  FormRegistr,
  FormRegistrList,
  FormRegistrItem,
  FormRegistrLabel,
  FormRegistrInput,
  Message,
  ButtonContainer,
  RegistrButton,
  PasswordButton,
  ShowPassword,
  HidePassword,
  ModalStyle,
  ContainerRegistrStyle,
} from './RegistrationForm.styled';

const initialValues = {
  name: '',
  email: '',
  password: '',
};

const RegistrationForm = ({handleClose}) => {
  const [show, setShow] = useState(false);

  const handleClick = () => setShow(!show);
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const formik = useFormik({
    initialValues,

    validationSchema: Yup.object({
      name: Yup.string()
        .min(3, ' 3 ')
        .max(254, ' 254 ')
        .matches(
          /[A-z]/,
          ' ,    ',
        )
        .required("'"),
      email: Yup.string()
        .email('  ')

        .max(254, ' 254 ')

        .matches(
          /([a-z0-9_.-]{3,})@([A-z0-9_.-]{1,}).([A-z]{2,8})/,
          '     3 ',
        )
        .required("'"),
      password: Yup.string()
        .min(8, ' 8 ')
        .max(100, ' 100 ')
        .matches(
          /(?=.*[0-9])(?=.*[a-z])[0-9a-zA-Z]{8,}/,
          // .matches(
          //   /(?=.*[0-9])(?=.*[a-z]{8,})/,
          '          ',
        )
        .required("'"),
    }),

    onSubmit: values => {
      const { name, email, password } = values;
      dispatch(authOperations.actionRegister({ name, email, password })).then(
        ({ payload }) => {
          if (payload?.code === 201) {
            navigate('/', { replace: true });
          }
        },
      );
      formik.resetForm();
    },
  });

    return (
    <ModalStyle>
            <ContainerRegistr>
                <ContainerRegistrStyle>
                    <CloseBtnPosition>
                        <CloseBtn onClick={handleClose}>
                            <CloseImg src={Close} alt="Close"/>
                        </CloseBtn>
                    </CloseBtnPosition>
                    <TitleRegistr></TitleRegistr>
                    <FormRegistr onSubmit={formik.handleSubmit}>
                        <FormRegistrList>
                        <FormRegistrItem>
                            <FormRegistrLabel htmlFor="name">' *</FormRegistrLabel>
                            <FormRegistrInput
                            id="name"
                            name="name"
                            type="text"
                            // pattern="([A-z])"
                            onChange={formik.handleChange}
                            value={formik.values.name}
                            />
                            {formik.touched.name && formik.errors.name ? (
                            <Message>{formik.errors.name}</Message>
                            ) : null}
                        </FormRegistrItem>
                        <FormRegistrItem>
                            <FormRegistrLabel htmlFor="email">
                              *
                            </FormRegistrLabel>
                            <FormRegistrInput
                            id="email"
                            name="email"
                            type="text"
                            //pattern="([a-z0-9_.-]{1,})@([A-z0-9_.-]{1,}).([A-z]{2,8})"
                            //title="       ,     ."
                            onChange={formik.handleChange}
                            value={formik.values.email}
                            />
                            {formik.touched.email && formik.errors.email ? (
                            <Message>{formik.errors.email}</Message>
                            ) : null}
                        </FormRegistrItem>
                        <FormRegistrItem>
                            <FormRegistrLabel htmlFor="password"> *</FormRegistrLabel>
                            <FormRegistrInput
                            id="password"
                            name="password"
                            type={show ? 'text' : 'password'}
                            // pattern="((?=.*[0-9])(?=.*[a-z])[0-9a-zA-Z]{8,})"
                            // title="          "
                            onChange={formik.handleChange}
                            value={formik.values.password}
                            />
                            <PasswordButton type="button" onClick={handleClick}>
                            {show ? <ShowPassword /> : <HidePassword />}
                            </PasswordButton>
                            {formik.touched.password && formik.errors.password ? (
                            <Message>{formik.errors.password}</Message>
                            ) : null}
                        </FormRegistrItem>
                        </FormRegistrList>
                        <ButtonContainer>
                        <RegistrButton type="submit"></RegistrButton>
                        </ButtonContainer>
                        </FormRegistr>
            </ContainerRegistrStyle>
        </ContainerRegistr>
    </ModalStyle>
  );
};
export default RegistrationForm;
